"use client";

import CertificateCard from "./CertificateCard"; // Importa la tarjeta de certificado
import certificatesData from "./certificates"; // Importa los datos de certificados
import Image from "next/image";
import { useTheme } from "../../hooks/useTheme"; // Hook para manejar tema (dark/light)

export default function CertificatesPage() {
  const theme = useTheme(); // Obtiene el tema actual

  return (
    <div className="p-6 relative">
      {/* ðŸ”¹ Imagen de la esquina superior derecha */}
      <Image
        src={
          theme === "dark"
            ? "/assets/dark/certifications.svg"
            : "/assets/light/certifications.svg"
        }
        alt="Certificaciones"
        width={100}
        height={100}
        priority // Optimiza la carga
        className="absolute top-0 right-0"
      />

      {/* ðŸ”¹ TÃ­tulo */}
      <h1 className="text-2xl font-bold text-center mb-6">Tus Certificaciones</h1>

      {/* ðŸ”¹ Contenedor de certificaciones */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {certificatesData.map((certificate) => (
          <CertificateCard key={certificate.id} {...certificate} />
        ))}
      </div>
    </div>
  );
}
