const CertificateCard = ({
    title,
    subtitle,
    status,
    image,
    lockedImage,
    descriptionLocked,
    descriptionUnlocked,
  }) => {
    const theme = useTheme(); // Obtiene el tema actual
  
    // Icono de bloqueo segÃºn el tema
    const lockIcon =
      theme === "dark"
        ? "/assets/dark/lock.svg"
        : "/assets/light/lock.svg";
  
    return (
      <div className={`p-6 border rounded-lg shadow-md transition-opacity`}>
        {/* ðŸ”¹ TÃ­tulo y subtÃ­tulo */}
        <h2 className="text-lg font-bold">{title}</h2>
        <p className="text-sm text-gray-500">{subtitle}</p>
  
        {/* ðŸ”¹ Imagen de certificado */}
        <img
          src={status === "completed" ? image : lockedImage}
          alt={title}
          className="w-full h-auto my-4"
        />
  
        {/* ðŸ”¹ DescripciÃ³n segÃºn el estado */}
        <p className="text-sm">
          {status === "completed" ? descriptionUnlocked : descriptionLocked}
        </p>
  
        {/* ðŸ”¹ BotÃ³n de descarga si estÃ¡ desbloqueado */}
        {status === "completed" && (
          <button className="mt-4 px-4 py-2 border rounded-lg text-primary">
            Descargar Certificado
          </button>
        )}
      </div>
    );
  };
  
  // ðŸ”¹ Exportar correctamente
  export default CertificateCard;
  